body, html, #app {
  font-family: "Open Sans", sans-serif;
  height: 100%;
  background-color: #eaf7ff;
}
button, input, textarea {
  outline: none;
}
textarea {
  resize: none;
  border: none;
}
#app {
  padding-top: 35px;
}
#screen {
  margin: 0 auto;
  width: 400px;
  padding: 18px;
  height: calc(100% - 135px);
  border-radius: 20px;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: white;
}
#screen::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}
#screen:hover .buttons {
    opacity: 1;
}
.row {
  width: 100%;
  clear: both;
  position: relative;
}
.row:hover .edit-button, .row:hover .delete-button {
    display: block;
}
.buttons {
  opacity: 0;
  margin-top: 10px;
  margin-bottom: 200px;
  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
}
#left-button {
  width: 30%;
  margin-right: 40%;
  height: 3em;
  float: left;
  cursor: pointer;
  border-radius: 17px 17px 17px 5px;
  -webkit-transition: all 130ms cubic-bezier(0.12, 1.11, 0.46, 0.99);
  transition: all 130ms cubic-bezier(0.12, 1.11, 0.46, 0.99);
  background-color: #f9f9f9;
}
#left-button:hover {
    background-color: #ddd;
}
#right-button {
  width: 30%;
  height: 3em;
  float: left;
  cursor: pointer;
  border-radius: 17px 17px 5px 17px;
  -webkit-transition: all 130ms cubic-bezier(0.12, 1.11, 0.46, 0.99);
  transition: all 130ms cubic-bezier(0.12, 1.11, 0.46, 0.99);
  background-color: #e6f5ff;
}
#right-button:hover {
    background-color: #009dff;
}
.buttons-div {
  margin: 35px auto 0 auto;
  width: 400px;
}
.generate-button {
  display: block;
  width: 280px;
  height: 40px;
  cursor: pointer;
  float: left;
  border-radius: 90px;
  border: none;
  color: #009dff;
  background-color: white;
  letter-spacing: 0.5px;
  -webkit-box-shadow: 0px 3px 7px 1px rgba(0, 124, 201, 0.1);
  box-shadow: 0px 3px 7px 1px rgba(0, 124, 201, 0.1);
  -webkit-transition: all 150ms cubic-bezier(0.76, 0.18, 0.23, 0.96);
  transition: all 150ms cubic-bezier(0.76, 0.18, 0.23, 0.96);
}
.generate-button:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
    -webkit-box-shadow: 0px 5px 8px 1px rgba(0, 124, 201, 0.08);
    box-shadow: 0px 5px 8px 1px rgba(0, 124, 201, 0.08);
}
.generate-button:active {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    -webkit-box-shadow: 0px 0px 0px 0px rgba(0, 124, 201, 0.3);
    box-shadow: 0px 0px 0px 0px rgba(0, 124, 201, 0.3);
}
.reset-button {
  display: block;
  width: 100px;
  height: 40px;
  margin-left: 20px;
  cursor: pointer;
  float: left;
  border-radius: 90px;
  border: none;
  color: #ff3838;
  background-color: white;
  letter-spacing: 0.5px;
  -webkit-box-shadow: 0px 3px 7px 1px rgba(0, 124, 201, 0.1);
  box-shadow: 0px 3px 7px 1px rgba(0, 124, 201, 0.1);
  -webkit-transition: all 150ms cubic-bezier(0.76, 0.18, 0.23, 0.96);
  transition: all 150ms cubic-bezier(0.76, 0.18, 0.23, 0.96);
}
.reset-button:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
    -webkit-box-shadow: 0px 5px 8px 1px rgba(0, 124, 201, 0.08);
    box-shadow: 0px 5px 8px 1px rgba(0, 124, 201, 0.08);
}
.reset-button:active {
    -webkit-transition: all 50ms cubic-bezier(0.76, 0.18, 0.23, 0.96);
    transition: all 50ms cubic-bezier(0.76, 0.18, 0.23, 0.96);
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    -webkit-box-shadow: 0px 0px 0px 0px rgba(0, 124, 201, 0.3);
    box-shadow: 0px 0px 0px 0px rgba(0, 124, 201, 0.3);
}
